<template>
  <div class="wrapper">
    <div class="box">
      <div class="title title01"></div>
      <div class="content">
        <ul>
          <li v-for="(data, index) in datas" :key="index" @click="gotolink">
            <label>☆</label>
            <label>{{ data.name }}</label>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <div class="content">
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
        <div class="bot"></div>
      </div>
    </div>
    <div class="box">
      <div class="img">
        <span>天津市</span>
        <span>路径规划</span>
        <span>资源查询</span>
        <span>清除地图</span>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      datas: [
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '2019年10月29日13:15:42'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '1508965232'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '园区基地A区危化品储存罐存放区'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '由于厂区员工操作失误，导致储存罐甲醇泄露，并引发火灾事故。'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '2019年10月29日13:15:42'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '1508965232'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '园区基地A区危化品储存罐存放区'
        },
        {
          name: '发生时间按适当阿萨德刚按适当则是哈爱上:',
          value: '由于厂区员工操作失误，导致储存罐甲醇泄露，并引发火灾事故。'
        }
      ]
    }
  },
  components: {},
  methods: {
    gotolink () {
      this.$map.clearLayers(this.$map.constslayer.alarmLayer)
      this.$map.drawPointToMap({
        x: 117.2009,
        y: 39.2243,
        ico: { name: './static/arcgismap/assets/img/marker.png', height: 30, width: 22 },
        layerId: this.$map.constslayer.alarmLayer,
        isCenter: true,
        click: () => { this.$router.replace('/scheme') }
      })
      // 指定跳转地址
      // this.$router.replace('/home2')
    }
  },
  mounted () { }
}
</script>

<style lang="stylus" scoped>
@import '../../assets/stylus/react.css';

.wrapper {
  width: 100%;
  height: 90%;
  // background red
  display: flex;

  // pointer-events: none;
  .box {
    width: 40vh;
    height: 28vh;
    // background: #008000;
    margin: 10px 0px 0 20px;
    pointer-events: auto;

    .title {
      -webkit-border-image: url('../../assets/images/home/renwuzhanshi.png') 0% 0% 0% 0% stretch;
      width: 180px;
      height: 50px;
    }

    .content {
      margin: 22px 0 0 28px;
      color: #FFFFFF;

      ul li {
        padding: 0px 0px 0px 16px;
        border: 1px solid #1D7295;
        height: 34px;
        line-height: 34px;
        margin: 6px 0 0 0px;
        background-color: rgba(6, 44, 65, 0.5);
        cursor: pointer;
      }

      ul li:hover {
        background: rgba(158, 131, 36, 0.5);
        color: #FFCB1E;
        border: 1px solid #9E8324;
      }
    }
  }

  .box:nth-child(1) {
    height: 96%;
  }

  .box:nth-child(2) {
    width: 56%;
    // background red
    align-self: flex-end;

    .content {
      display: flex;
      width: 1920px;
      height: 100%;
      align-items: center;
      background: url('../../assets/images/home/bottom.png') no-repeat;
      position: relative;
      top: 26%;
      left: -41.5%;

      .bot {
        width: 130px;
        height: 130px;
        // background-color: #f00;
        margin: 0 0 0 24px;
      }
    }
  }

  .box:nth-child(3) {
    height: 96%;
    justify-content: flex-end;
  }

  .img {
    width: 492px;
    height: 88px;
    background: url('../../assets/images/home/right111.png') no-repeat;
    margin: 0 0 0 -136px;
    span{
      color: #26befe;
      margin: 34px 0 0 20px;
      display: inline-block;
      cursor pointer
    }
    span:nth-child(2) {
      margin: 34px 0 0 44px;
    }
    span:nth-child(3) {
      margin: 34px 0 0 48px;
    }
    span:nth-child(4) {
      margin: 34px 0 0 44px;
    }
  }
}
</style>



